<template>
  <div class="block mt-3 mb-3">
    <b-alert
      :show="dismissCountDown"
      dismissible
      variant="success"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >
      <p>{{ tudibuchangfee }}</p>
      <p>{{ anzhibuzhufee }}</p>
      <p><strong>{{ finalfee }}</strong></p>
      <p><a href="../../static/fee.jpeg" target="_blank">()</a></p>
      <hr>

      <p></p>
      <p>  </p><a href="tel:18802035347">188 0203 5347</a>
      <b-progress
        variant="warning"
        :max="dismissSecs"
        :value="dismissCountDown"
        height="6px"
      ></b-progress>
    </b-alert>

    <b-alert v-model="showDismissibleAlert" variant="danger" dismissible>
      {{ message }}
    </b-alert>



    <b-form-group
      id="input-group-1"
      label=""
      label-for="input-1"
    >
      <el-cascader
        v-model="value2"
        :options="options"
        :props="{ expandTrigger: 'hover' }"

        weight="50px">
      </el-cascader>
    </b-form-group>



    <b-form-group  id="input-group-3" label="" label-for="input-2">
      <b-form-select v-model="selected" :options="options2" required></b-form-select>
    </b-form-group>


    <b-form-group id="input-group-2" label="" label-for="input-2" description="1666.7">
      <b-form-input
        id="input-2"
        placeholder=""
        required
        v-model="area"
        type="number"
      ></b-form-input>
    </b-form-group>

    <b-form @reset="onReset" >
    <b-button variant="success" block @click="showAlert"></b-button>
    <b-button variant="warning" block type="reset"></b-button>

    </b-form>

  </div>
</template>

<script>


export default {
  name: "forminfo",
  data() {
    return {
      dismissSecs: 60,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      area:'',
      tudibuchangfee:null,
      anzhibuzhufee:null,
      finalfee:'',
      selected:null,
      message:'',
      value2: [],
      options2: [
        { value: null, text: '' },
        { value: 'a', text: '' },
        { value: 'b', text: '' },
        { value: 'c', text: '' },
        { value: 'd', text: ''},
        { value: 'e', text: ''},
        { value: 'f', text: ''},
      ],
      options: [
        {
        value: '',
        label: '',
        children: [{
          value: '',
          label: ''
        },
         {
            value: '',
            label: ''
          },
          {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        },
          {
            value: '',
            label: ''
          },
          {
            value: '',
            label: ''
          },
          {
            value: '',
            label: ''
          },
          {
            value: '',
            label: ''
          },
          {
            value: '',
            label: ''
          },
          {
            value: '',
            label: ''
          }
        ]
      },
        {
          value: '',
          label: '',
          children: [{
            value: '',
            label: ''
          },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            }, {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
            {
              value: '',
              label: ''
            },
          ]
        },
      ]
    };
  },
  methods: {
    countDownChanged(dismissCountDown) {
      this.showDismissibleAlert=false
      if(this.value2[0] == ""){
        this.tudibuchangfee = parseFloat((this.area * 25.5).toFixed(2))
        this.anzhibuzhufee = parseFloat((this.area * 25.5).toFixed(2))
        this.finalfee = (this.tudibuchangfee + this.anzhibuzhufee)
        this.dismissCountDown = dismissCountDown
      }else{
        this.tudibuchangfee = parseFloat((this.area * 25.09).toFixed(2))
        this.anzhibuzhufee = parseFloat((this.area * 25.09).toFixed(2))
        this.finalfee = (this.tudibuchangfee + this.anzhibuzhufee)
        this.dismissCountDown = dismissCountDown
      }

    },
    showAlert() {
      if(this.area && this.selected){
        this.dismissCountDown = this.dismissSecs
        console.log()
      }else if(this.value == ""){
        this.message = ''
        this.showDismissibleAlert=true
      }else if(this.selected==""){
        this.message = ''
        this.showDismissibleAlert=true
      }else if(this.area==""){
        this.message = ''
        this.showDismissibleAlert=true
      }else{
        this.message = ""
        this.showDismissibleAlert=true
      }
    },
    handleChange(value) {
      console.log(value);
    },
    onReset(evt) {
      evt.preventDefault()
      // Reset our form values
      this.value2 = ''
      this.area = ''
      this.selected = null
    },
  },

}
</script>

<style scoped>

</style>



